import React from 'react'
import rusbitech from '../assets/rusbitech.jpg'
import kameleoon from '../assets/kameleoon.jpg'
import './experience.scss'


const Experience = () => {

  const workData = [
    {nameCompany: `РусБИТех`, namePost: `Инженер-программист`, icon: rusbitech, date: `Май 2023 - настоящее время`, descr: `Разработка новых проектов на ExtJS и поддержка существующих.`,},
    {nameCompany: `Kameleoon`, namePost: `JavaScript разработчик`, icon: kameleoon, date: `Апрель 2021 — Сентябрь 2022 (1 год и 6 месяцев)`, descr: `Разработка веб-элементов для A/B тестирования сайтов на JavaScript, поддержка и отладка кода, верстка.`, namePost2: `Тестировщик`, date2: `Февраль 2020 — Апрель 2021 (1 год и 3 месяца)`, descr2: `Мануальное тестирование веб-элементов на различных устройствах и браузерах.`},
  ]

  const workDataBlock = workData.map((workData, index) => {
    return (
      <div class="card">
        <div class="card__info">
          <div className="card__info-header">
            <img className='work-img' src={workData.icon}></img>
            <h2 class="title">{workData.nameCompany}</h2>
          </div>
     
        <div className="info-body">
          <div className="prof">
            <h3 className='profession-js'>{workData.namePost}</h3>
            <h4>{workData.date} </h4>
            <p>{workData.descr}</p>

            <h3 className='profession-qa'>{workData.namePost2}</h3>
            <h4>{workData.date2}</h4>
            <p>{workData.descr2}</p>
          </div>
        </div>
        </div>
      </div>
    )
}) 

  return (
    <div className='experience'>
      <span>Опыт работы</span>
      {workDataBlock}
    </div>
  )
}

export default Experience