import './aboutMe.scss'

const AboutMe = () => {
    return (
        <div className='about-me'>
            <span>Обо мне</span>
            <p className='about-me__text'>В 2020 году решил попробовать себя в IT-сфере. 1 год 3 месяца работал на позиции веб-тестировщика, после чего перевёлся внутри компании на позицию разработчика и проработал 1 год 6 месяцев. Совершенствую свои навыки <b>JavaScript</b>, также изучаю <b>React</b>. Легко вхожу в новый коллектив. В свободное от учёбы и работы время увлекаюсь футболом.</p>
        </div>
    )
}

export default AboutMe